.rename-modal .modal-overlay {
  backdrop-filter: blur(4px);
  animation: fadeIn 0.3s ease-out;
}
.rename-modal .modal-content {
  animation: slideUp 0.3s ease-out;
}
.rename-modal .rename-input {
  transition: all 0.2s ease;
}
.rename-modal .rename-input:focus {
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}
.rename-modal .rename-input.error {
  border-color: #ef4444;
  box-shadow: 0 0 0 3px rgba(239, 68, 68, 0.1);
}
.rename-modal .rename-button {
  transition: all 0.2s ease;
}
.rename-modal .rename-button:hover:not(:disabled) {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}
.rename-modal .rename-button:active {
  transform: translateY(0);
}
.rename-modal .rename-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.rename-modal .cancel-button {
  transition: all 0.2s ease;
}
.rename-modal .cancel-button:hover {
  background-color: #e5e7eb;
}
.rename-modal .close-button {
  transition: all 0.2s ease;
}
.rename-modal .close-button:hover {
  color: #6b7280;
  transform: scale(1.1);
}
.rename-modal .validation-message {
  animation: slideDown 0.2s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
